

let game = {};
;let engineService = () => {
  let engine = {};

  engine.initialize = ({canvas}) => {
    engine.canvas = canvas;
  };

  engine.logCanvas = () => { console.log(engine.canvas); };

  let renderService = () => {
    let render = {};

    let spritesByColor = new Map();

    render.addSprite = (sprite) => {
      if(!spritesByColor.has(sprite.color)){
        spritesByColor.set(sprite.color, []);
      }
      spritesByColor.get(sprite.color).push(sprite);
    };

    render.update = () => {
      let ctx = engine.canvas.getContext('2d');
      let drawColorGroup = (sprites, color) => {
        ctx.beginPath();
        ctx.fillStyle = color;
        sprites.forEach( (sprite) => {
          sprite.draw(ctx);
        });
        ctx.fill();
        ctx.closePath();
      };
      spritesByColor.forEach(drawColorGroup);
    };

    return render;
  };

  engine.render = renderService();



  return engine;
};

game.engine = engineService();
;class Sprite {
  constructor({type, position, color, radius=40}) {
    switch(type) {
      case 'circle':
        this.draw = function(ctx) {
          ctx.arc(this.position.x, this.position.y, this.radius, 0, 2*Math.PI);
        };
        break;
    }
    this.type = type;
    this.position = position;
    this.color = color;
    this.radius = radius;
  }
}
;var canvas = document.getElementById("slitherbots-canvas");

console.log(game);

game.engine.initialize({canvas: canvas});
game.engine.logCanvas();

let slitherbot = new Sprite({
  type: 'circle',
  position: {x: 0, y: canvas.height/3},
  color: 'blue'
});

let slitherbot2 = new Sprite({
  type: 'circle',
  position: {x: canvas.width, y: canvas.height/2},
  color: 'red'
});

game.engine.render.addSprite(slitherbot);
game.engine.render.addSprite(slitherbot2);


let move = () => {
  slitherbot.position.x+=1;
  slitherbot2.position.x-=1;
  game.engine.render.update();
};

window.setInterval( () => {
  window.requestAnimationFrame(move);
}, 10);

